/* Основные стили */
:root {
  --primary-color: #007bff;
  --secondary-color: #6c757d;
  --background: #f8f9fa;
  --card-bg: #ffffff;
  --text-primary: #212529;
  --text-secondary: #6c757d;
  --border-color: #dee2e6;
  --box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  --border-radius: 8px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: var(--background);
  color: var(--text-primary);
  line-height: 1.6;
}

/* Хедер */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  background: var(--card-bg);
  padding: 1rem;
  box-shadow: var(--box-shadow);
  z-index: 1000;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.back-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  padding: 0.5rem;
  color: var(--primary-color);
  cursor: pointer;
}

.title {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
}

/* Список досок */
.board-list {
  padding: 5rem 1rem 1rem;
  display: grid;
  gap: 1rem;
}

.board-item {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  box-shadow: var(--box-shadow);
  transition: transform 0.2s;
}

.board-item:hover {
  transform: translateY(-2px);
}

.board-item a {
  text-decoration: none;
  color: inherit;
}

.board-code {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}

.board-name {
  font-size: 0.9rem;
  color: var(--text-secondary);
}

/* Карточка треда */
.thread-card {
  display: block;
  text-decoration: none;
  color: inherit;
  background: var(--card-bg);
  border-radius: var(--border-radius);
  margin: 1rem;
  padding: 1rem;
  box-shadow: var(--box-shadow);
}

.threads-list {
  padding: 5rem 1rem 1rem;
}

.posts-list {
  padding: 5rem 1rem 1rem;
}

.thread-stats {
  font-size: 0.85rem;
  color: var(--text-secondary);
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
}

/* Карточка поста */
.post-card {
  background: var(--card-bg);
  border-radius: var(--border-radius);
  margin: 1rem;
  padding: 1rem;
  box-shadow: var(--box-shadow);
}

.post-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.post-num {
  font-weight: 600;
  color: var(--primary-color);
}

.post-date {
  font-size: 0.8rem;
}

.post-content {
  position: relative;
}

.media-preview {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.media-preview img {
  max-width: 120px;
  height: auto;
  border-radius: 4px;
  cursor: pointer;
  transition: transform 0.2s;
}

.media-preview img:hover {
  transform: scale(1.05);
}

.media-meta {
  font-size: 0.75rem;
  color: var(--text-secondary);
  width: 100%;
}

.post-text {
  word-break: break-word;
}

.post-text ::after {
  content: "";
  display: table;
  clear: both;
}

/* Модальные окна */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  backdrop-filter: blur(3px);
}

.modal-content {
  background: var(--card-bg);
  padding: 1.5rem;
  border-radius: var(--border-radius);
  max-width: 90%;
  max-height: 90vh;
  overflow: auto;
  position: relative;
}

.modal-content img {
  max-width: 100%;
  max-height: 70vh;
  border-radius: 4px;
}

.media-info {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.save-btn {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 1rem;
  width: 100%;
}

/* Лоадер */
.loader {
  padding: 5rem 1rem;
  display: flex;
  justify-content: center;
}

.loader-ring {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 4px solid var(--border-color);
  border-radius: 50%;
  border-top-color: var(--primary-color);
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Текст элементы */
.post-link {
  color: var(--primary-color);
  text-decoration: underline;
  cursor: pointer;
}

button {
  background: var(--primary-color);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: opacity 0.2s;
}

button:hover {
  opacity: 0.9;
}

/* Адаптация для мобилок */
@media (max-width: 480px) {
  .board-list {
    padding-top: 4rem;
  }
  
  .post-card,
  .thread-card {
    margin: 0.5rem;
    padding: 0.75rem;
  }
  
  .media-preview img {
    max-width: 100px;
  }
}

/* Кастомные теги */
strong, b {
  font-weight: 600;
}

em, i {
  font-style: italic;
}

sub {
  vertical-align: sub;
  font-size: smaller;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

.spoiler {
  background: #000;
  color: #000;
  cursor: pointer;
}

.spoiler:hover {
  color: #fff;
}

.unkfunc {
  color: #789922;
  font-style: italic;
}

.s { text-decoration: line-through; }
.u { text-decoration: underline; }
.o { text-decoration: overline; }

/* Добавить в index.css */
.header-controls {
  display: flex;
  gap: 0.5rem;
  margin-left: auto;
}

.dropdown {
  position: relative;
}

.dropdown-content {
  position: absolute;
  right: 0;
  top: 100%;
  background: var(--card-bg);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  min-width: 160px;
  z-index: 1000;
}

.dropdown-content button {
  background: none;
  color: var(--text-primary);
  text-align: left;
  padding: 0.5rem;
  border-radius: 4px;
}

.dropdown-content button:hover {
  background: var(--background);
}

.notification {
  position: fixed;
  top: 4rem;
  left: 50%;
  transform: translateX(-50%);
  background: var(--primary-color);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: var(--border-radius);
  z-index: 1000;
  animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
  from { top: -2rem; opacity: 0; }
  to { top: 4rem; opacity: 1; }
}

.error-message {
  color: #dc3545;
  padding: 1rem;
  background: #ffeef0;
  margin: 1rem;
  border-radius: var(--border-radius);
}

.refresh-btn {
  padding: 0.25rem 0.5rem;
  font-size: 1.2rem;
}

.menu-btn {
  padding: 0.25rem 0.75rem;
}

.board-link {
  display: block;
  padding: 1rem;
  text-decoration: none;
  color: inherit;
}

.board-link:hover {
  background-color: rgba(0, 0, 0, 0.05);
}
